<style scoped>

</style>

<template>
    <div>
        <h3> 角色授权</h3>
        <Divider orientation="left">选择角色权限</Divider>
        <Row>
            <Col :xs="24" :sm="24" :md="12" :lg="12">
                <Form ref="form" :label-width="80">
                    <FormItem label="权限">
                        <div v-for="(permission,index) in permissions" :key="permission.id">
                            <checkbox
                                    v-model="permission.checked"
                                    @on-change="checkChange(permissions[index])">{{permission.name}}
                            </checkbox>
                            <div style="margin-left: 50px" v-for="(child,indexC) in permissions[index].child"
                                 :key="child.id">
                                <Checkbox @on-change="checkChange(permissions[index].child[indexC],permissions[index])"
                                          v-model="child.checked">{{child.name}}
                                </Checkbox>
                                <div style="margin-left: 20px">
                                    <Checkbox
                                            @on-change="checkChange(permissions[index].child[indexC].child[indexI],permissions[index].child[indexC],permissions[index])"
                                            v-model="item.checked"
                                            v-for="(item,indexI) in permissions[index].child[indexC].child"
                                            :key="item.id">
                                        {{item.name}}
                                    </Checkbox>
                                </div>
                            </div>
                        </div>
                        <Button type="primary" style="margin-left: 50px"
                                @click="onSubmitPer">提交
                        </Button>
                        <Button plain style="margin-left: 50px"
                                @click="$router.back()">返回
                        </Button>
                    </FormItem>
                </Form>
                <Spin size="large" fix v-if="isLoading"></Spin>
            </Col>

        </Row>
    </div>
</template>

<script>
import api from './api'

export default {
  name: 'rolePer',
  data () {
    return {
      permissionsData: {},
      permissions: {},
      isLoading: false,
      permission_id: []
    }
  },
  mounted () {
    this.loadPermission()
  },
  methods: {
    loadPermission () {
      this.isLoading = true
      api.permissions({ id: this.$route.params.id }, res => {
        this.isLoading = false
        this.permissions = res.data.data
        this.initCheck(this.permissions)
      }, () => {
        this.isLoading = false
      })
    },
    initCheck (permissions) {
      permissions.forEach(value => {
        value.checked = value.role_status === 1
        if (value.child != null) {
          this.initCheck(value.child)
        }
      })
    },

    setCheck (permissions, va) {
      permissions.forEach(value => {
        value.role_status = va
        value.checked = va === 1
        if (value.child != null) {
          this.setCheck(value.child, va)
        }
      })
    },
    onSubmitPer () {
      this.setPermissionId(this.permissions)
      let data = { id: this.$route.params.id, permission_id: this.permission_id }
      this.isLoading = true
      api.assignRolePermission(data, () => {
        this.isLoading = false
        this.$Message.success('授权成功')
        this.$router.push({ name: 'role' })
      }
        , () => {
        this.isLoading = false
      })
    },
    setPermissionId (pers) {
      pers.forEach(value => {
        if (value.checked) {
          this.permission_id.push(value.id)
          if (value.child != null) {
            this.setPermissionId(value.child)
          }
        }
      })
    },
    checkChange (value, fa, ga) {
      let va = value.checked ? 1 : 0
      value.role_status = va
      value.checked = va === 1
      if (value.child != null) {
        this.setCheck(value.child, va)
      }
      if (value.checked) {
        if (fa != null) {
          fa.checked = true
          fa.role_status = 1
        }
        if (ga != null) {
          ga.checked = true
          ga.role_status = 1
        }
      }
      // let p = this.$formUtil.deepCopy(this.permissions);
      let p = this.permissions
      this.permissions = []
      this.$nextTick(() => {
        this.permissions = p
        // this.permissions=this.$formUtil.deepCopy(this.permissions);
      })
    }
  }
}
</script>
